<template>
  <div class="hello">
    <scroller :on-refresh="refresh" :on-infinite="infinite" ref="myscroller">
    <Header></Header>
      <van-swipe :autoplay="3000" class="swipe">
        <van-swipe-item></van-swipe-item>
        <van-swipe-item></van-swipe-item>
        <van-swipe-item></van-swipe-item>
        <van-swipe-item></van-swipe-item>
      </van-swipe>    
      <ul class="content" v-for="(item, index) in response" :key="index">
        <li id="linkto" @click="linkto(item.id)">{{item.title}}</li>
        <img :src='item.images[0]' >
      </ul>
    </scroller>
  </div>
  
</template>

<script>
import { Swipe, SwipeItem, Lazyload } from 'vant';
import VueScroller from 'vue-scroller'
import vue from 'vue';
vue.use(Swipe).use(SwipeItem).use(Lazyload).use(VueScroller);
import Header from './Header'
export default {
  name: 'HelloWorld',
  components:{
    Header: Header
  },
  data () {
    return {
        response: [],
    }
  },
  created() {
     this.getData();
          },
  methods:{
    getData(){
      this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/last-stories',          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories)
                console.log(res)
            })
    },
    beforeData(){
      this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/before-stories/20190603',          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories)
                console.log(res)
          })
    },
    linkto(id){
      this.$router.push({path:'/details/'+id})
    },
    infinite(done) {
      var vm = this;
      setTimeout(()=>{
          vm.beforeData();
          done();
        },1000)
    },
    refresh() {
      this.response =[];
      setTimeout(() => {
        this.getData();
        this.$refs.myscroller.finishPullToRefresh();
      }, 500);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#linkto{
  cursor: pointer;
}
#linkto:hover{
  color: orange;
}
.swipe{
  width: 100%;
  height: 3rem;;
  background: pink
}
li{
  font-size: 0.35rem;
}
.content{
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
}
.content li{
  padding-top: 0.5rem;
}
.content >>>img{
  width: 2rem;
  height: 2rem;
}
</style>



